h1,
h2,
h3,
h4,
h5,
blockquote,
a,
p,
ol,
ul,
li,
fieldset,
form,
label,
legend,
article {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ul {
  list-style: none;
}

button {
  border: 0;
  background-color: transparent;
}

a {
  text-decoration: none;
  color: inherit;
}
